body {
    font-family: 'Saira Semi Condensed', sans-serif;
    background-color: #000 !important
}

.tick {
    position: relative;
    top: 0.5vh
}

.main {
    height: auto;
    width: 35vw;
    background-color: white;
    margin: auto;
    position: relative;
    top: 15vh;
    padding: 3vh
}

.picture img {
    height: 8vw;
    width: 8vw;
    position: relative;
    top: 1vw
}

.add {
    background-color: #43a047;
    position: relative;
    top: 3vw;
    right: 2vw
}

.name {
    font-size: 20px
}

.details p {
    position: relative;
    top: 1vw
}

.line {
    padding-right: 21px; /* 20+1 */
    position: relative;
}
.line:after {
    content: '';
    position: absolute;
    right: 0;
    border-right: 1px solid #cfc7c0;
    top: 10%;
    bottom: 10%;
}

.progress-label {
  float: left;
  margin-right: 1em;
}

.name i {
    color: blue
}

td {
    text-align: center
}

tr {
    border: 2px solid white
}

.table {
    margin-top: 2vw
}

.edit {
    background-color: #43a047;
    width: 90%;
    color: #fff !important
}
.white-border { border: 2px solid white; }
.thin-border { border-top: 1px solid #ddd; }
.progress-width { width: 95%; }

@media only screen and (max-width: 425px) {
    .main {
        width: 100vw;
        height: auto
    }

    .picture img {
        height: 40vw;
        width: 40vw;
        position: relative;
        left: 5vw
    }

    .details p {
        position: relative;
        top: 5vw
    }

    .add {
        position: relative;
        top: 8vw
    }
}

@media only screen and (max-height: 725px) {
    .main {
        height: auto
    }
}

body {
    background-color: #B3E5FC;
    border-radius: 10px
}

.card {
    width: 400px;
    border: none;
    border-radius: 10px;
